.c-uploader {
  cursor: default;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  border: 0.25rem dashed #a5a5c7;
  text-align: center;
  padding: 2rem;
  max-width: 25rem;
  &.active {
    border-color: red;
    border-style: solid;
  }
}

.c-uploader__text {
  color: #999;
  margin-top: 2rem;
  margin-bottom: 2rem;
  font-size: 2rem;
}

.c-uploader__browser {
  margin-bottom: 2rem;
}

.c-uploader__or {
  display: none;
}

.c-uploader__button {
  position: relative;
  overflow: hidden;
  height: 34px;
  line-height: 34px;
  font-size: 14px;
  border: 1px solid #ccc;
  background-color: #fff;
  color: #333;
  text-align: center;
  vertical-align: middle;
  border-radius: 4px;
  &:active,
  &:focus,
  &:hover {
    color: #333;
    outline: 0;
    background-color: #e6e6e6;
    border-color: #adadad;
  }
}

.c-uploader__shadow {
  position: absolute;
  top: 0;
  right: 0;
  margin: 0;
  border: solid transparent;
  width: 100%;
  opacity: .0;
  filter: alpha(opacity=0);
  cursor: pointer;
}

.fn-hide {
  display: none;
}


